<template>
<div class="save">
  <div class="header">
    <span>我的持仓</span>
  </div>
  <div v-show="change&&sellBuy.buyVol>0" class="contentBuy">
    <span>买</span>
    <span>{{sellBuy.buyPriceJ}}</span>
    <span>{{sellBuy.buyVol}}手</span>
    <span :class="{'changeGre':sellBuy.buyMoney<0}">{{sellBuy.buyMoney}}$</span>
    <span @click="tosaveDtail(0)">平仓</span>
  </div>
  <div class="bottom" @click="toChange" v-show="change">
    <span>查看所有持仓&#xe7d1;</span>
  </div>
</div>
</template>

<script>
  import Vue from 'vue';
  import pingCang from './pingCang'
  import { Stepper } from 'vant';
  Vue.use(Stepper);
  export default {
    data(){
      return {
        max:0,
        change:true,
        value:0,
        showPing:false,
        text:'',
        price:0,
        money:0,
        marginCas:0,
        lastList:{
          name:'',
          money:0,  //浮动盈亏
          num:0,
          text:'',
          Jprice:0,   //开仓均价
          vol:0
        },
        newPrice:0,
        closeprice:0,
        holdPositions2:{},
        Money:0
      }
    },
    props:{
      margin:{
        type:Number
      },
      sellBuy:{
        type:Object
      },
      tickPrice:{           //当前合约tick

      },
      tickName:{        //合约名称汉字
        type:String
      },
      ticknameT:{        //合约名称英文
        type:String
      },
      holdPositions:{

      }
    },
    components:{
      pingCang
    },
    methods:{
      chooseAll(){
        if(this.text==='卖'){
          this.value=this.sellBuy.sellVol;
        }else if(this.text==='买'){
          this.value=this.sellBuy.buyVol;
        }
      },
      refresh(){
        this.$emit('reload');
        // this.showPing=false;
        // this.change=true;
      },
      toPing(){
        this.showPing=true;
        this.lastList.name=this.tickName+this.ticknameT;
        this.lastList.money=this.money;
        this.lastList.text=this.text;
        this.lastList.num=this.value;
        this.lastList.Jprice=this.price
        if(this.text==='卖'){
          this.lastList.vol=this.sellBuy.sellVol;
        }else if(this.text==='买'){
          this.lastList.vol=this.sellBuy.buyVol;
        }
        for(let i=0;i<this.holdPositions.length;i++){
          if(this.holdPositions[i].instrumentid===this.ticknameT){
            if(this.holdPositions[i].direction===this.text){
              this.holdPositions2=this.holdPositions[i];
              console.log(this.holdPositions2)
            }
          }
        }
      },
      hidePing(){
        this.showPing=false;
      },
      tosaveDtail(num){
        if(num===0){
          this.text='买';
          this.price=this.sellBuy.buyPriceJ;
          this.max=this.sellBuy.buyVol;
          this.Money=this.sellBuy.buyMoney;
        }else {
          this.text='卖';
          this.price=this.sellBuy.sellPriceJ;
          this.max=this.sellBuy.sellVol;
          this.Money=this.sellBuy.sellMoney;
        };
        let this_=this;
        this.$router.push({
          name:'saveDtail',
          params:{
            text:this_.text
          }
        })
      }
    },
    watch:{
      tickPrice(newVal){
        this.newPrice=newVal['price'];
        this.closeprice=newVal['price']-newVal['preclose'];
        // console.log(this.text)
        if(this.text==='买'){
          this.money=(this.sellBuy.priceperpoint*this.sellBuy.rate*this.value*(newVal['price']-this.sellBuy.buyPriceJ)).toFixed(2);
          this.lastList.money=this.money;
        }else if(this.text==='卖') {
          this.money=(this.sellBuy.priceperpoint*this.sellBuy.rate*this.value*(newVal['price']-this.sellBuy.sellPriceJ)).toFixed(2)
          this.lastList.money=this.money;
        }
      },
      value(newVal){
        this.marginCas=Number(newVal)*this.margin;
        if(this.text==='买'){
          this.money=(this.sellBuy.priceperpoint*this.sellBuy.rate*newVal*(this.tickPrice['price']-this.sellBuy.buyPriceJ)).toFixed(2);
          console.log(this.money)
        }else {
          this.money=(this.sellBuy.priceperpoint*this.sellBuy.rate*newVal*(this.tickPrice['price']-this.sellBuy.sellPriceJ)).toFixed(2)
          console.log(this.money)
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "../../style/theme2";
  .save {
    width: 100%;
    background-color: #FFF;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 6;
    padding-bottom:tovw(48);
    @media only screen and (device-width: 375px) and (device-height: 812px){
      padding-bottom:tovw(72);
    }
    .header {
      width: 100%;
      height:tovw(50);
      line-height: tovw(50);
      padding-left: tovw(20);
      border-bottom: tovw(1) solid #e3e1e1;
      border-top: tovw(1) solid #F4F4F4;
      font-family: 'iconfont'!important;
      position: relative;
      font-size: tovw(16);
      font-weight: 600;
      color: #2F2F2F;
    }
    .contentBuy {
      width: 100%;
      height: tovw(56);
      background-color: #FFF;
      border-bottom: tovw(1) solid #F4F4F4;
      padding:tovw(16) tovw(20);
      position: relative;
      &>span:nth-child(1){
        display: inline-block;
        width: tovw(17);
        height: tovw(17);
        border-radius: tovw(4);
        text-align: center;
        background-color: #FE0000;
        font-size: tovw(10);
        line-height: tovw(20);
        color: #FFF;
        vertical-align: top;
        margin-top: tovw(2);
      }
      &>span:nth-child(2){
        display: inline-block;
        vertical-align: top;
        line-height: tovw(23);
        width: tovw(85);
        height: tovw(23);
        padding-left: tovw(10);
        vertical-align: top;
      }
      &>span:nth-child(3){
        display: inline-block;
        line-height: tovw(23);
        /*width: tovw(60);*/
        height: tovw(23);
        padding-left: tovw(5);
        vertical-align: top;
      }
      &>span:nth-child(4){
        display: inline-block;
        line-height: tovw(23);
        /*width: tovw(80);*/
        position: absolute;
        right: tovw(85);
        height: tovw(23);
        padding-left: tovw(10);
        color:#FE0000;
        vertical-align: top;
        &.changeGre {
          color:#25B100;
        }
      }
      &>span:nth-child(5){
        display: inline-block;
        position: absolute;
        right: tovw(20);
        width: tovw(60);
        height: tovw(23);
        margin-left: tovw(5);
        font-size: tovw(14);
        line-height: tovw(23);
        padding: tovw(1) tovw(0) tovw(0) tovw(0);
        text-align: center;
        color:#FFF;
        background-color: #1988FF;
        border-radius: tovw(4);
        vertical-align: top;
      }
    }
    .contentSell {
      width: 100%;
      height: tovw(56);
      background-color: #FFF;
      border-bottom: tovw(1) solid #F4F4F4;
      padding:tovw(16) tovw(20);
      position: relative;
      &>span:nth-child(1){
        display: inline-block;
        width: tovw(17);
        height: tovw(17);
        border-radius: tovw(4);
        text-align: center;
        background-color: #25B100;
        font-size: tovw(10);
        line-height: tovw(20);
        color: #FFF;
        vertical-align: top;
        margin-top: tovw(2);
      }
      &>span:nth-child(2){
        display: inline-block;
        line-height: tovw(23);
        width: tovw(85);
        height: tovw(23);
        padding-left: tovw(10);
        vertical-align: top;
      }
      &>span:nth-child(3){
        display: inline-block;
        line-height: tovw(23);
        /*width: tovw(60);*/
        height: tovw(23);
        padding-left: tovw(5);
        vertical-align: top;
      }
      &>span:nth-child(4){
        display: inline-block;
        line-height: tovw(23);
        /*width: tovw(80);*/
        position: absolute;
        right: tovw(85);
        height: tovw(23);
        padding-left: tovw(10);
        color:#FE0000;
        &.changeGre {
          color:#25B100;
        }
      }
      &>span:nth-child(5){
        display: inline-block;
        position: absolute;
        right: tovw(20);
        width: tovw(60);
        height: tovw(23);
        margin-left: tovw(5);
        font-size: tovw(14);
        line-height: tovw(23);
        padding: tovw(1) tovw(0) tovw(0) tovw(0);
        text-align: center;
        color:#FFF;
        background-color: #1988FF;
        border-radius: tovw(4);
        vertical-align: top;
      }
    }
    .bottom {
      font-family: 'iconfont'!important;
      width: 100%;
      text-align: center;
      &>span {
        height: tovw(60);
        line-height: tovw(60);
        font-size: tovw(14);
        color: #999999;
      }
    }
   }
</style>



